:root {
    --container-back: #fff;
    --primary-dark: #f5f5f5;
    --primary: #fff;
    --accent: #3cb4e0;
    --progress-back: #f0f0f0;
    --extra: #a2a3a7;
    --button-background: #fff;
}

.no-mt { margin-top: initial !important; }

.sup-header { font-size: 2rem; font-family: Muli,Roboto,"Helvetica",Arial,sans-serif; font-weight: 600; line-height: 1.6; padding: 20px; }
.sec-desc { font-size: 1.5rem; font-family: Muli,Roboto,"Helvetica",Arial,sans-serif; font-weight: 400; line-height: 1.6; padding: 20px; margin-bottom: 15px; }
.sup-header-edit { padding: 0 20px; margin-bottom: 15px; }
.sup-back-link { display: inline-block; width: 35px; color: #0386ba; border: solid 2px; border-radius: 100%; height: 35px; font-weight: bold; font-size: 1.5rem; padding: 4px; vertical-align: sub; position: relative; z-index: 1; }

.section-container { padding: 30px; width: 100%; }
.section-container .facitlity-detail { width: 100%; }
.section-container .img-container { width: 100px; height: 100px; border-radius: 100%; overflow: hidden; }
.section-container .img-container img { width: 100%; height: 100%; display: block; }
.section-container .detail-section { vertical-align: top; margin-left: 20px; margin-top: 5px; }
.section-container .name-header { font-weight: bold; }
.section-container .secondary-header { font-weight: bold; margin-top: 30px; margin-bottom: 15px; }
.section-container .third-header {font-weight: bold;margin-top: 10px;margin-bottom: 10px; font-size: 1.5rem; }

.section-container .supervision-list-block { margin-top: 20px; width: 100%; counter-reset: sectionCounter;}
.section-container .section-item { padding: 5px 15px 5px 50px; border: solid 1px #ccc; border-radius: 5px; counter-increment: sectionCounter; position: relative; }
.section-container .section-item:hover, .section-container .section-item:focus { border-color: var(--accent); border-width: 2px; outline: none; }
.section-container .section-item + .section-item { margin-top: 10px; }
.section-container .section-item::before { position: absolute; content: counter(sectionCounter); left: 0; top: 0; font-size: 1.5rem; display: block; width: 50px; height: 50px; text-align: center; line-height: 50px; }
.section-container .count-span, .section-container .score-span { color: grey; display: inline-block; margin-bottom: 5px; }
.section-container .score-span { float: right; }
.section-container .custom-pos { position: absolute; width: calc(100% - 60px); top: 150px; }

.row-container { display: flex; gap: 15px; }
.row-container + .row-container { margin-top: 15px; }


.section-container .MuiFormControl-root + * { margin-top: 15px; }
.section-container .form-error { color: red; }

.section-container label { margin-bottom: 5px; }
.section-container label .optional { color: grey; }

.section-container .form-input { padding: 5px; }
.section-container .MuiSelect-outlined { padding-top: 10.5px; padding-bottom: 10.5px; }

.section-container .btn-container { margin: 30px 0; width: 100%; text-align: center; }
.section-container .save-btn { width: 100px; height: 35px; color: white; background: #039be5; }
.section-container .save-btn:hover { opacity: 0.8; }

.section-container .MuiTableCell-body:nth-child(1), .section-container .MuiTableCell-head:nth-child(1) { width: 50px !important; }
/* .section-container .MuiTableCell-body:nth-child(2), .section-container .MuiTableCell-head:nth-child(2) { width: 50px !important; } */
.section-container .MuiTableCell-body:nth-child(2), .section-container .MuiTableCell-head:nth-child(2) { width: calc(100% - 100px) !important; }
.section-container .MuiTableCell-body:nth-child(3), .section-container .MuiTableCell-head:nth-child(3) { width: 50px !important; }
.td-element { display: table; table-layout: fixed; width: 100%; }
.truncate { overflow: hidden; text-overflow: ellipsis; white-space: nowrap; }

.detail-panel { padding: 30px; }
.save-btn { width: 150px; height: 30px; background: #039be5; color: white; margin-top: 20px; }

.option-table-container { width: 100%; border: solid 1px #cccccc; border-radius: 5px; }
.option-table { width: 100%; }
.option-table thead th { padding: 10px; background: #039be5; color: white; text-align: center; }

.option-table .month-col { width: 20%; }
.option-table .male-col { width: 20%; }
.option-table .female-col { width: 20%; }
.option-table .child-col { width: 20%; }
.option-table .total-col { width: 20%; }

.option-table .MuiOutlinedInput-notchedOutline { border: none !important; }

.option-table td { border: solid 1px #cccccc; text-align: center; }
.option-table td:focus-within { border: solid 2px black; }
.option-table .error-col { border: solid 2px red !important; }
.mb-20 { margin-bottom: 20px; }

@media (max-width: 320px) {
    .section-container .count-span, .section-container .score-span { float: none; display: block; }
}

@media ( max-width: 500px) {
    .section-container .img-container { display: block; margin: auto; }
    .section-container .detail-section { text-align: center; margin-left: 0; display: block; }
}

@media (max-width: 768px) {
    .custom-pos { position: initial !important; width: 100% !important; justify-content: start !important; margin-top: 20px; margin-bottom: -20px; }
    .row-container { display: block; }
    .section-container .MuiFormControl-root + * { margin-top: 15px !important; }
}